import { Component, OnInit, ViewChild } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { UtilsService } from 'src/app/services/utils.service';
import { AppValueService } from 'src/app/services/app-value.service';
import { TranslateService } from '@ngx-translate/core';
import { EventManagementService } from './event-management.service';
import { ModalEditEventComponent } from 'src/app/components/modal-edit-event/modal-edit-event.component';

@Component({
  selector: 'app-event-management',
  templateUrl: './event-management.page.html',
  styleUrls: ['./event-management.page.scss'],
})
export class EventManagementPage implements OnInit {
  @ViewChild('contentSearch') contentSearch: any;  
  heightTable = "775";
  constructor(private popoverController: PopoverController,
    public eventService: EventManagementService,
    public utilsService: UtilsService,
    public appValueService: AppValueService,
    private translate: TranslateService) { }

  ngOnInit() {
  }
  showDetail(data: any, mode: any) {
    if(mode == "edit"){
      this.showModalEditEvent(data.ev, data.row._row.data);
    }
    else {
      this.showModalEditEvent(null, null);
    }
    
  }

  async showModalEditEvent(ev: any, row: any) {
    const popover = await this.popoverController.create({
      component: ModalEditEventComponent,
      id: "editEventModal",
      event: ev,
      translucent: true,
      backdropDismiss: false,
      componentProps: { rowProduct: row },
      cssClass: "edit-event-popover"
    });
    return await popover.present();
  }
  ionViewDidEnter() {
    this.heightTable = this.contentSearch.el.offsetHeight;
  }
}
